@charset 'utf-8';

.tui-pagination * {
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.tui-ico-ellipsis,
.tui-ico-first,
.tui-ico-last,
.tui-ico-next,
.tui-ico-prev {
  overflow: hidden;
}

.tui-pagination {
  height: 28px;
  margin: 14px 0 12px;
  line-height: normal;
  text-align: center;
  font-size: 0;
}

.tui-pagination .tui-page-btn {
  display: inline-block;
  position: relative;
  width: 40px;
  height: 40px;
  padding: 13px 2px 11px;
  margin-left: 0;
  color: #000000;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33;
  text-decoration: none;
  vertical-align: middle;
  border-width: 1px 0;
  border-radius: 5px;

  // outline: 2px solid tomato;
  margin-right: 5px;

  transition: background var(--animation), color var(--animation);

  &:hover,
  &:active,
  &:focus {
    color: var(--main-accent-color);
  }
}

// .tui-pagination .tui-page-btn:not(:last-child) {
//   margin-right: 5px;
// }

.tui-pagination .tui-page-btn:hover {
  color: #fff;
  background-color: var(--main-accent-color);
  border-radius: 5px;
}

.tui-pagination .tui-is-selected,
.tui-pagination strong {
  background-color: var(--main-accent-color);
  border-radius: 5px;
  color: #fff;
  background: var(--main-accent-color);
  border-color: var(--main-accent-color);
  cursor: default;
}

.tui-pagination .tui-is-selected:hover {
  color: #fff;
  background-color: var(--main-accent-color);
  border-radius: 5px;
}

.tui-pagination .tui-first-child.tui-is-selected,
.tui-pagination strong.tui-first-child {
  margin-left: 0;
}

.tui-pagination .tui-first,
.tui-pagination .tui-prev,
.tui-pagination .tui-next,
.tui-pagination .tui-last,
.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
  overflow: hidden;
  height: 40px;
  font-size: 0;
  line-height: 26px;
  white-space: nowrap;
  font-weight: normal;
  width: 40px;
  padding: 6px 13px 10px 13px;
}

.tui-pagination .tui-prev-is-ellip {
  border-right: 0;
}

.tui-pagination .tui-next-is-ellip {
  border-left: 0;
}

.tui-pagination .tui-first + .tui-prev,
.tui-pagination .tui-next + .tui-last {
  border-left: 0;
  width: 40px;
  height: 40px;
  padding: 6px 13px 10px 13px;
}

.tui-pagination .tui-ico-first,
.tui-pagination .tui-ico-prev,
.tui-pagination .tui-ico-next,
.tui-pagination .tui-ico-last,
.tui-pagination .tui-ico-ellip {
  background: url(data:image/png;base64,)
    no-repeat 0 0;
  display: inline-block;
  vertical-align: middle;
  font-size: 0px;
}

.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
  padding: 3px 13px 10px 13px;
  line-height: 1;
  width: 40px;
  height: 40px;
}

.tui-pagination .tui-prev {
  margin-right: 10px;
}

.tui-pagination .tui-next {
  margin-left: 10px;
  width: 40px;
  height: 40px;
  padding: 6px 13px 10px 13px;
}

.tui-pagination .tui-ico-first {
  width: 11px;
  height: 10px;
  background-position: 0px 0px;
}

.tui-pagination .tui-ico-prev {
  width: 6px;
  height: 10px;
  background-position: -26px 0px;
}

.tui-pagination .tui-ico-next {
  width: 6px;
  height: 10px;
  background-position: -13px -24px;
}

.tui-pagination .tui-ico-last {
  width: 11px;
  height: 10px;
  background-position: 0px -12px;
}

.tui-pagination .tui-ico-ellip {
  width: 10px;
  height: 2px;
  margin-top: 16px;
  vertical-align: top;
  background-position: 0px -24px;
}

.tui-pagination .tui-is-disabled:hover {
  background: #fff;
  cursor: default;
}

.tui-pagination .tui-is-disabled .tui-ico-prev {
  background-position: -26px -12px;
}

.tui-pagination .tui-is-disabled .tui-ico-first {
  background-position: -13px 0px;
}

.tui-pagination .tui-is-disabled .tui-ico-next {
  background-position: -21px -24px;
}

.tui-pagination .tui-is-disabled .tui-ico-last {
  background-position: -13px -12px;
}

.tui-page-inputs {
  display: inline-block;
  vertical-align: top;
}

.tui-page-inputs .tui-page-input {
  display: inline-block;
  width: 10px;
  height: 12px;
  padding: 8px 0 6px;
  font-size: 12px;
  text-align: center;
  color: #333;
  border: 0;
  outline: none;
}

.tui-page-inputs .tui-page-current {
  color: var(--main-accent-color);
}

.tui-page-separator {
  display: inline-block;
  font-size: 12px;
  height: 12px;
  padding: 8px 0 6px;
  vertical-align: top;
}

.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip,
.tui-pagination .tui-last,
.tui-pagination .tui-first {
  @include mq(mob-only) {
    display: none;
  }
}

.tui-pagination .tui-page-btn,
.tui-pagination .tui-first,
.tui-pagination .tui-prev,
.tui-pagination .tui-next,
.tui-pagination .tui-last,
.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip,
.tui-pagination .tui-first + .tui-prev,
.tui-pagination .tui-next + .tui-last {
  @media screen and (max-width: 480px) {
    margin: 0;
  }
}

.library__pagination {
  visibility: hidden;
}
